.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}
.section-box {
    background-color: #f8f9fa;
    border-radius: 10px;
    padding: 20px;
    position: relative;
    height: 100%;
}
.section-title {
    color: #007bff;
    margin-bottom: 20px;
}
.progress-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.progress-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e9ecef;
}
.progress-chart {
    width: 60px;
    height: 60px;
    margin-right: 15px;
}
.progress-info {
    flex-grow: 1;
}
.course-title {
    font-weight: bold;
    display: block;
}
.course-registered {
    color: #6c757d;
    font-size: 0.9em;
}
.progress-percentage {
    color: #007bff;
    font-weight: bold;
    float: right;
}
.see-all {
    position: absolute;
    top: 20px;
    right: 20px;
    color: #007bff;
    text-decoration: none;
}
.notice-list {
    list-style-type: none;
    padding: 0;
}
.notice-list li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    border-bottom: 1px solid #e9ecef;
}
.badge {
    background-color: #007bff;
    color: white;
    padding: 3px 8px;
    border-radius: 5px;
}
.notice-time {
    color: #6c757d;
}
