.lecture-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
}

.lecture-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.lecture-header h2 {
    margin: 0;
}

.lecture-header .date {
    font-size: 1.2rem;
    color: #555;
}

.lecture-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f9f9f9;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-bottom: 10px;
}

.lecture-item h3 {
    margin: 0;
    font-size: 1.2rem;
}

.lecture-item .time {
    font-size: 1rem;
    color: #777;
}

.lecture-item .actions {
    display: flex;
    gap: 10px;
}

.lecture-item .actions a,
.lecture-item .actions button {
    padding: 8px 15px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border: none;
    border-radius: 5px;
    font-size: 0.9rem;
    cursor: pointer;
}

.lecture-item .actions a:hover,
.lecture-item .actions button:hover {
    background-color: #0056b3;
}
